.dropbtn {
    padding: 5px;
    border: none;
    cursor: pointer;
  }
  
  .dropdown {
    position: relative;
    display: inline-block;
  }
  
  .dropdown-content {
    display: none;
    position: absolute;
    /* right: 100%; */
    top: 15;
    background-color: #f9f9f9;
    min-width: 160px;
    box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
    z-index: 9990;
    padding: 10px;
    border-top: 5px solid #ff9900;
  }
  
  .dropdown-content a {
    color: black;
    padding: 5px;
    text-decoration: none;
    display: block;
  }
  
  .dropdown-content a:hover {
    
  }
  
  .dropdown:hover>.dropdown-content {
    display: block;
  }
  
  .dropdown:hover .dropbtn {
    
  }